.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 80vw;
  max-width: 600px;
  height: 80vh;
  padding: 20px;
  z-index: -3;
  z-index: 999;
  transform: translate(-50%, -50%);
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
  display: none;
  border: 1px solid rgba(0, 0, 0, 0.5);
}
.modal-section {
  display: block;
  z-index: 3;
  background: #fff;
}
.modal-container {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.modal-close {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 10px;
  right: 10px;
  font-size: 1.2rem;
  color: #333;
  padding: 0.3rem;
  background-color: transparent;
  border-color: transparent;
  cursor: pointer;
}
.modal-close:hover {
  background-color: #000;
  color: #fff;
  border-radius: 50%;
}
.modal-item {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: auto;
}
.modal-item > h3 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.5rem 0;
}
.modal-img {
  flex: 0 40%;
  display: flex;
  max-height: 40%;
}
.single-img {
  flex: 1;
  object-fit: fill;
}
.modal-info {
  flex: 2 50%;
  display: flex;
  flex-direction: column;
}

.coffee-row {
  display: flex;
  margin-top: 5px;
  margin-bottom: 5px;
  flex: 0 40px;
  border: 1px solid rgba(0, 0, 0, 0.3);
}
.coffee-row > h3 {
  flex: 0 40%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-right: 1px solid rgba(0, 0, 0, 0.3);
  font-size: 1rem;
}
.btn-row {
  flex: 1;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.btn-row button:disabled {
  background-color: #d3d3d3;
  color: #a9a9a9;
  cursor: not-allowed;
  border-radius: 50%;
}
.minus,
.plus {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border-color: transparent;
  cursor: pointer;
  padding: 0.4rem;
  color: #000;
}
.minus:hover,
.plus:hover {
  background-color: #000;
  color: #fff;
  border-radius: 50%;
}
.coffee-size {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  text-transform: uppercase;
  background-color: transparent;
  border: 1px solid rgba(0, 0, 0, 0.2);
  cursor: pointer;
}
.coffee-size:hover {
  background-color: #000;
  color: #fff;
}
.active-size {
  background-color: lightgray;
}
.minus {
  margin-left: 18px;
}
.plus {
  margin-right: 18px;
}
.order-btn {
  flex: 0 40px;
  width: 100%;
  padding: 8px;
  background-color: #007bff;
  font-size: 1.2rem;
  text-align: center;
  border-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  color: #fff;
  font-size: 1rem;
  border-radius: 5px;
  margin-top: 6px;
  letter-spacing: 3px;
  transition: all 0.3s linear;
}
.order-btn:hover {
  background-color: #0056b3;
  color: yellow;
}

@media screen and (min-width: 1100px) {
  .modal {
    max-width: 700px;
  }
}
